import React, { useState, useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import Vapi from '@vapi-ai/web';
import { Button } from '../../../components/ui/button';
import { Badge } from '../../../components/ui/badge';

import {
  Phone,
  PhoneOff,
  Mic,
  MicOff,
  Volume2,
  Loader2,
  MessageSquare,
  User,
  Bot,
  AlertCircle
} from 'lucide-react';
import { toast } from 'sonner';

interface VapiAgentCallProps {
  agentId: string;
  agentName?: string;
  agentConfig?: any;
  onCallStart?: () => void;
  onCallEnd?: () => void;
  onError?: (error: any) => void;
}

interface CallState {
  isConnected: boolean;
  isConnecting: boolean;
  isMuted: boolean;
  volumeLevel: number;
  callDuration: number;
  status: 'idle' | 'connecting' | 'connected' | 'ended' | 'error';
}

interface TranscriptMessage {
  role: 'user' | 'assistant' | 'system';
  content: string;
  timestamp: Date;
  type?: 'transcript' | 'function-call' | 'status';
}

const VapiAgentCall: React.FC<VapiAgentCallProps> = ({
  agentId,
  agentName = 'AI Assistant',
  agentConfig,
  onCallStart,
  onCallEnd,
  onError
}) => {
  // Get VAPI public key from environment
  const publicApiKey = import.meta.env.VITE_VAPI_PUBLIC_KEY || 'eb3b26e3-7dbb-4fd6-a545-a954756a43aa';

  // VAPI instance ref
  const vapiRef = useRef<Vapi | null>(null);

  // Call state
  const [callState, setCallState] = useState<CallState>({
    isConnected: false,
    isConnecting: false,
    isMuted: false,
    volumeLevel: 0,
    callDuration: 0,
    status: 'idle'
  });

  // Transcript and messages
  const [transcript, setTranscript] = useState<TranscriptMessage[]>([]);

  // Call duration timer
  const callTimerRef = useRef<NodeJS.Timeout | null>(null);

  // Check if VAPI is properly configured
  const isVapiConfigured = publicApiKey && publicApiKey !== 'your-vapi-public-key-here';

  // Debug VAPI availability
  console.log('üîç VAPI Debug Info:');
  console.log('- Vapi class available:', typeof Vapi);
  console.log('- Public API Key:', publicApiKey ? `${publicApiKey.substring(0, 8)}...` : 'Not set');
  console.log('- Is configured:', isVapiConfigured);

  // Initialize VAPI
  useEffect(() => {
    console.log('üîß Initializing VAPI for agent:', agentId);
    console.log('üîë Public API Key:', publicApiKey ? `${publicApiKey.substring(0, 8)}...` : 'Not set');
    console.log('‚úÖ Is VAPI Configured:', isVapiConfigured);

    // Test VAPI import
    try {
      console.log('üß™ Testing VAPI import...');
      console.log('- Vapi constructor:', Vapi);
      console.log('- typeof Vapi:', typeof Vapi);

      if (typeof Vapi === 'function') {
        console.log('‚úÖ VAPI import successful');
      } else {
        console.error('‚ùå VAPI import failed - not a constructor');
      }
    } catch (error) {
      console.error('‚ùå VAPI import test failed:', error);
    }

    if (isVapiConfigured) {
      try {
        vapiRef.current = new Vapi(publicApiKey);
        console.log('‚úÖ VAPI instance created successfully');

        // Set up event listeners
        setupVapiEventListeners();

      } catch (error) {
        console.error('‚ùå Failed to initialize VAPI:', error);
        toast.error('Failed to initialize VAPI. Please check your API key.');
      }
    } else {
      console.warn('‚ö†Ô∏è VAPI public key not configured');
    }

    return () => {
      if (vapiRef.current) {
        vapiRef.current.stop();
      }
      if (callTimerRef.current) {
        clearInterval(callTimerRef.current);
      }
    };
  }, [publicApiKey, agentId]);

  // Setup VAPI event listeners
  const setupVapiEventListeners = () => {
    if (!vapiRef.current) return;

    // Call start event
    vapiRef.current.on('call-start', () => {
      console.log('üéôÔ∏è VAPI call started');
      setCallState(prev => ({
        ...prev,
        isConnected: true,
        isConnecting: false,
        status: 'connected'
      }));
      
      // Start call timer
      startCallTimer();
      
      toast.success('Call connected! You can now speak.');
      onCallStart?.();
    });

    // Call end event
    vapiRef.current.on('call-end', () => {
      console.log('üìû VAPI call ended');
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        status: 'ended'
      }));
      
      // Stop call timer
      if (callTimerRef.current) {
        clearInterval(callTimerRef.current);
        callTimerRef.current = null;
      }
      
      toast.info('Call ended');
      onCallEnd?.();
    });

    // Speech start event
    vapiRef.current.on('speech-start', () => {
      console.log('üó£Ô∏è Speech started');
    });

    // Speech end event
    vapiRef.current.on('speech-end', () => {
      console.log('ü§ê Speech ended');
    });

    // Volume level event
    vapiRef.current.on('volume-level', (volume: number) => {
      setCallState(prev => ({
        ...prev,
        volumeLevel: volume
      }));
    });

    // Message event (transcripts, function calls, etc.)
    vapiRef.current.on('message', (message: any) => {
      console.log('üì® VAPI message:', message);
      
      if (message.type === 'transcript' && message.transcript) {
        const transcriptMessage: TranscriptMessage = {
          role: message.role || 'assistant',
          content: message.transcript,
          timestamp: new Date(),
          type: 'transcript'
        };
        
        setTranscript(prev => [...prev, transcriptMessage]);
      }
    });

    // Error event
    vapiRef.current.on('error', (error: any) => {
      console.error('‚ùå VAPI error:', error);
      setCallState(prev => ({
        ...prev,
        isConnected: false,
        isConnecting: false,
        status: 'error'
      }));

      // Provide helpful error messages based on common issues
      let errorMessage = 'Unknown error';
      if (error.message) {
        errorMessage = error.message;

        // Check for common error patterns
        if (error.message.includes('assistant') && error.message.includes('not found')) {
          errorMessage = 'Assistant not found in VAPI. Please ensure the agent is properly synced with VAPI dashboard.';
        } else if (error.message.includes('unauthorized') || error.message.includes('401')) {
          errorMessage = 'Unauthorized access. Please check your VAPI API key configuration.';
        } else if (error.message.includes('microphone') || error.message.includes('permission')) {
          errorMessage = 'Microphone permission denied. Please allow microphone access and try again.';
        }
      }

      toast.error(`Call error: ${errorMessage}`);
      onError?.(error);
    });
  };

  // Start call timer
  const startCallTimer = () => {
    callTimerRef.current = setInterval(() => {
      setCallState(prev => ({
        ...prev,
        callDuration: prev.callDuration + 1
      }));
    }, 1000);
  };

  // Format call duration
  const formatDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  // Create assistant configuration for VAPI
  const createAssistantConfig = () => {
    // Create a basic assistant configuration
    const assistantConfig = {
      name: agentName,
      firstMessage: agentConfig?.firstMessage || `Hello! I'm ${agentName}. How can I help you today?`,
      model: {
        provider: 'openai' as const,
        model: 'gpt-4' as const,
        temperature: 0.7,
        messages: [{
          role: 'system' as const,
          content: agentConfig?.systemMessage || `You are ${agentName}, a helpful AI assistant. Keep responses conversational and under 30 words when possible.`
        }]
      },
      voice: {
        provider: 'vapi' as const,
        voiceId: agentConfig?.voiceId || 'Cole'
      }
    };

    console.log('üìã Assistant config created:', assistantConfig);
    return assistantConfig;
  };

  // Start web call with agent
  const startWebCall = async () => {
    console.log('üöÄ Starting VAPI agent call for:', agentId);
    console.log('üîë VAPI Public Key:', publicApiKey ? `${publicApiKey.substring(0, 8)}...` : 'Not configured');
    console.log('ü§ñ Agent Config:', agentConfig);

    if (!vapiRef.current) {
      toast.error('VAPI not initialized. Please check your API key configuration.');
      return;
    }

    if (!isVapiConfigured) {
      toast.error('VAPI public key not configured. Please set VITE_VAPI_PUBLIC_KEY in your environment.');
      return;
    }

    try {
      setCallState(prev => ({
        ...prev,
        isConnecting: true,
        status: 'connecting',
        callDuration: 0
      }));

      setTranscript([]);

      // Check if we have an assistant_id in the agent config
      const assistantId = agentConfig?.assistant_id || agentConfig?.assistantId;

      if (assistantId) {
        // Use existing assistant ID from database
        console.log('üìû Using existing assistant ID from database:', assistantId);

        // For VAPI Web SDK, when using assistantId, pass it directly as string
        // You can pass assistant overrides as second parameter if needed
        const assistantOverrides = {
          metadata: {
            portalAgentId: agentId,
            agentName: agentName,
            initiatedFrom: 'portal'
          }
        };

        console.log('üìã Starting call with assistant ID:', assistantId);
        console.log('üìã Assistant overrides:', assistantOverrides);
        await vapiRef.current.start(assistantId, assistantOverrides);

      } else {
        // Create assistant configuration dynamically
        console.log('üîÑ Creating assistant configuration...');
        const assistantConfig = createAssistantConfig();

        console.log('üìû Starting agent call with assistant config');
        console.log('ü§ñ Agent Name:', agentName);

        // Use VAPI's assistant configuration directly
        const callConfig = assistantConfig;

        console.log('üìã Call config:', callConfig);

        // Add a small delay to ensure VAPI is ready
        await new Promise(resolve => setTimeout(resolve, 100));

        await vapiRef.current.start(callConfig);
      }

    } catch (error: any) {
      console.error('‚ùå Error starting agent call:', error);
      setCallState(prev => ({
        ...prev,
        isConnecting: false,
        status: 'error'
      }));

      // Provide more specific error messages
      let errorMessage = 'Failed to start call';
      if (error.message) {
        errorMessage = error.message;

        // Check for common error patterns
        if (error.message.includes('Authentication') || error.message.includes('401')) {
          errorMessage = 'Authentication failed. Please check your VAPI API key.';
        } else if (error.message.includes('assistant') || error.message.includes('404')) {
          errorMessage = 'Assistant not found. The assistant may not exist in VAPI.';
        } else if (error.message.includes('permission') || error.message.includes('microphone')) {
          errorMessage = 'Microphone permission denied. Please allow microphone access.';
        } else if (error.message.includes('network') || error.message.includes('fetch')) {
          errorMessage = 'Network error. Please check your internet connection.';
        }
      } else if (error.code) {
        errorMessage = `Error code: ${error.code}`;
      } else if (typeof error === 'string') {
        errorMessage = error;
      }

      toast.error(`Failed to start call: ${errorMessage}`);
      console.error('üìã Full error details:', error);
    }
  };

  // Stop web call
  const stopWebCall = () => {
    console.log('üõë Stopping VAPI agent call...');
    
    if (vapiRef.current) {
      vapiRef.current.stop();
    }
  };

  // Toggle mute
  const toggleMute = () => {
    if (vapiRef.current) {
      const newMutedState = !callState.isMuted;
      vapiRef.current.setMuted(newMutedState);
      
      setCallState(prev => ({
        ...prev,
        isMuted: newMutedState
      }));
      
      toast.info(newMutedState ? 'Microphone muted' : 'Microphone unmuted');
    }
  };

  // If VAPI is not configured, show error state
  if (!isVapiConfigured) {
    return (
      <div className="flex flex-col items-center justify-center h-[400px] text-center space-y-4">
        <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center">
          <AlertCircle className="h-8 w-8 text-red-600" />
        </div>
        <div>
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Call Not Available</h3>
          <p className="text-gray-600 mb-4">This agent doesn't support web calls yet</p>
          <div className="text-sm text-gray-500 space-y-1">
            <p>To enable web calls:</p>
            <p>1. Configure your VAPI public key</p>
            <p>2. Set up the agent in VAPI dashboard</p>
            <p>3. Ensure the agent ID is valid</p>
          </div>
        </div>
        <Badge variant="destructive" className="mt-4">
          VAPI Configuration Required
        </Badge>
      </div>
    );
  }

  // Render call button in header
  const renderCallButton = () => {
    // if (!portalReady) {
    //   console.log('üéØ Portal not ready yet');
    //   return null;
    // }

    const container = document.getElementById('call-button-container');
    console.log('üéØ Looking for call-button-container:', container);
    if (!container) {
      console.warn('‚ö†Ô∏è call-button-container not found!');
      return null;
    }

    console.log('üéØ Rendering call button, call state:', callState);
    return createPortal(
      <div className="flex items-center gap-2">
        {callState.isConnected && (
            <div className="flex items-center gap-3">
              <div className="text-sm font-mono font-semibold text-gray-800 bg-white px-3 py-1 rounded-md border border-gray-200">
                {formatDuration(callState.callDuration)}
              </div>
              <Button
                onClick={toggleMute}
                variant="outline"
                size="sm"
                className={`border-gray-300 ${
                  callState.isMuted
                    ? 'bg-red-50  text-red-700 hover:bg-red-100'
                    : 'bg-white hover:bg-gray-50 text-gray-700'
                }`}
              >
                {callState.isMuted ? (
                  <MicOff className="h-4 w-4" />
                ) : (
                  <Mic className="h-4 w-4" />
                )}
              </Button>

              <Button
                variant="outline"
                size="sm"
                className="border-gray-300 bg-white hover:bg-gray-50 text-gray-700"
              >
                <Volume2 className="h-4 w-4" />
              </Button>
            </div>
          )}
        {!callState.isConnected && !callState.isConnecting ? (
          <Button
            onClick={startWebCall}
            size="sm"
            className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white text-sm font-medium rounded-md transition-all duration-200"
          >
            <Phone className="h-4 w-4 mr-2" />
            Call
          </Button>
        ) : callState.isConnecting ? (
          <Button
            size="sm"
            className="px-4 py-2 bg-amber-500 text-white text-sm font-medium rounded-md"
            disabled
          >
            <Loader2 className="h-4 w-4 mr-2 animate-spin" />
            Connecting
          </Button>
        ) : (
          <Button
            onClick={stopWebCall}
            size="sm"
            className="px-4 py-2 bg-red-600 hover:bg-red-700 text-white text-sm font-medium rounded-md transition-all duration-200"
          >
            <PhoneOff className="h-4 w-4 mr-2" />
            End Call
          </Button>
        )}
      </div>,
      container
    );
  };

  console.log('üéØ VapiAgentCall render - isVapiConfigured:', isVapiConfigured);
  console.log('üéØ VapiAgentCall render - callState:', callState);
  console.log('üéØ VapiAgentCall render - agentId:', agentId);
  console.log('üéØ VapiAgentCall render - agentName:', agentName);

  // State to track if portal container is available
  const [portalReady, setPortalReady] = useState(false);

  // Check for portal container availability
  useEffect(() => {
    const checkContainer = () => {
      const container = document.getElementById('call-button-container');
      if (container) {
        console.log('‚úÖ Found call-button-container');
        setPortalReady(true);
        return true;
      } else {
        console.warn('‚ùå call-button-container not found');
        return false;
      }
    };

    // Check immediately
    if (checkContainer()) {
      return;
    }

    // If not found, check periodically
    const interval = setInterval(() => {
      if (checkContainer()) {
        clearInterval(interval);
      }
    }, 100);

    // Clean up
    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className="flex flex-col h-full bg-white">
      {renderCallButton()}

      {/* Debug: Show call button directly in component */}
    

      {/* Scrollable Live Transcript */}
      <div className="flex-1 flex flex-col overflow-hidden">
        <div className="bg-gray-50 border-b border-gray-200 px-6 py-3">
          <h3 className="text-sm font-semibold text-gray-700 flex items-center gap-2">
            <MessageSquare className="h-4 w-4" />
            Conversation
          </h3>
        </div>

        <div className="flex-1 overflow-y-auto p-6 bg-white">
          {transcript.length === 0 ? (
            <div className="flex flex-col items-center justify-center h-full text-gray-400 py-12">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                <MessageSquare className="h-8 w-8 text-gray-400" />
              </div>
              <p className="text-center text-sm">
                {callState.isConnected
                  ? 'Conversation will appear here...'
                  : 'Start a call to begin the conversation'}
              </p>
            </div>
          ) : (
            <div className="space-y-6">
              {transcript.map((message, index) => (
                <div
                  key={index}
                  className={`flex gap-3 ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  {message.role === 'assistant' && (
                    <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center flex-shrink-0">
                      <Bot className="h-4 w-4 text-gray-600" />
                    </div>
                  )}
                  <div
                    className={`max-w-[75%] ${
                      message.role === 'user'
                        ? 'bg-blue-600 text-white'
                        : 'bg-gray-100 text-gray-800 border border-gray-200'
                    } rounded-lg px-4 py-3`}
                  >
                    <div className="flex items-center gap-2 mb-1">
                      <span className={`text-xs font-medium ${
                        message.role === 'user' ? 'text-blue-100' : 'text-gray-500'
                      }`}>
                        {message.role === 'user' ? 'You' : agentName}
                      </span>
                      <span className={`text-xs ${
                        message.role === 'user' ? 'text-blue-200' : 'text-gray-400'
                      }`}>
                        {message.timestamp.toLocaleTimeString()}
                      </span>
                    </div>
                    <p className="text-sm leading-relaxed">{message.content}</p>
                  </div>
                  {message.role === 'user' && (
                    <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center flex-shrink-0">
                      <User className="h-4 w-4 text-blue-600" />
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Agent Information Footer */}
      <div className="bg-gray-50 border-t border-gray-200 px-6 py-4">
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <p className="text-gray-500 text-xs uppercase tracking-wide mb-1">Assistant</p>
            <p className="font-medium text-gray-900">{agentName}</p>
          </div>
          <div>
            <p className="text-gray-500 text-xs uppercase tracking-wide mb-1">Connection</p>
            <div className="flex items-center gap-2">
              <div className={`w-2 h-2 rounded-full ${
                callState.status === 'connected' ? 'bg-green-500' :
                callState.status === 'connecting' ? 'bg-amber-500 animate-pulse' :
                callState.status === 'error' ? 'bg-red-500' :
                'bg-gray-400'
              }`}></div>
              <span className="text-gray-700 capitalize">
                {callState.status === 'idle' ? 'Ready' :
                 callState.status === 'connecting' ? 'Connecting' :
                 callState.status === 'connected' ? 'Active' :
                 callState.status === 'ended' ? 'Ended' : 'Error'}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default VapiAgentCall;
